<template>
	<view class="box">
		<image lazy-load class="top-img" src="../../static/images/heath_banner.png" mode="aspectFill"></image>
		<view class="good-classified">
			<!-- 商品列表 -->
			<view class="goods">
				<mescroll-body ref="mescrollRef" @init="mescrollInit" @down="downCallback" @up="upCallback" :up="upOption" :down="{'use':false}">
					<view class="goods-box">
						<navigator v-for="(item,k) in data_list" class="goods-li" :url="'/pages/goods/detail?seqId='+item.seqId+'&merchantShopId='+item.merchantShopId"
						 :key="k">
							<image lazy-load class="img scale45" :src="item.mainPicUrl" mode="aspectFill"></image>
							<view class="title-box">
								<view class="tips-box" v-if="data_list[k].spuLabel">
									<template v-for="(val,i) in data_list[k].spuLabel">
										<view class="width106 " :class="{'bg-btn':i==0,'border-btn':i==1}" :key="i">
											{{val}}
										</view>
									</template>
								</view>
								<view class="title">
									{{item.spuTitle}}
								</view>
								<view class="price-box">
									<view class="num font-bold">￥{{item.promptPrice | formatNum}}</view>
								</view>
							</view>
						</navigator>
					</view>
				</mescroll-body>
			</view>
		</view>


	</view>
</template>

<script>
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollBody from "@/components/mescroll-uni/mescroll-body.vue"
	export default {
		mixins: [MescrollMixin], // 使用mixin
		data() {
			return {
				data_list: [],
				upOption: {
					page: {
						size: 6
					},
					textNoMore: "没有更多数据",
				},
			}
		},
		components: {
			MescrollBody
		},
		onLoad() {
			uni.showLoading({
				title: '加载中'
			});
			setTimeout(function() {
				uni.hideLoading()
			}, 600)
		},
		methods: {
			upCallback(page) {
				let pageNum = page.num; // 页码, 默认从1开始
				let pageSize = page.size; // 页长, 默认每页10条
				this.$api.get('/o2oSpu/findHeadPage', {
					params: {
						pageno: pageNum,
						pagesize: pageSize,
						promptType: 'NONE',
						onSale: 'SALE',
						catCode: 'C049',
						pCatCode: 0

					}
				}).then((res) => {
					if (res.code && res.code != 200) {
						this.mescroll.endErr();
						return;
					}
					let data = res;
					// 接口返回的当前页数据列表 (数组)
					let curPageData = data.list;
					// 接口返回的当前页数据长度 (如列表有26个数据,当前页返回8个,则curPageLen=8)
					let curPageLen = curPageData.length;
					// 接口返回的总页数 (如列表有26个数据,每页10条,共3页; 则totalPage=3)
					let totalPage = data.pages;
					for (let val of curPageData) {
						if (val.spuLabel) {
							val.spuLabel = val.spuLabel.split(',')
						}
					}
					//设置列表数据
					if (page.num == 1) this.data_list = []; //如果是第一页需手动置空列表
					this.data_list = this.data_list.concat(curPageData)
					// 请求成功,隐藏加载状态
					//方法一(推荐): 后台接口有返回列表的总页数 totalPage
					this.mescroll.endByPage(curPageLen, totalPage);
				})
			}
		}

	}
</script>

<style lang="scss">
	.top-img {
		width: 100%;
		height: 310rpx;
	}

	.top-tips-box {
		width: 682rpx;
		height: 50rpx;
		background: rgba(255, 255, 255, 1);
		box-shadow: 0px 6rpx 4rpx 0px rgba(46, 46, 46, 0.13);
		border-radius: 6rpx;
		display: flex;
		font-size: 20rpx;
		align-items: center;
		margin: 0 auto;

		.text {
			font-size: 24rpx;
			font-weight: bolder;
			font-style: italic;
			background: linear-gradient(90deg, rgba(250, 98, 85, 1) 0%, rgba(252, 183, 176, 1) 100%);
			-webkit-background-clip: text;
			color: transparent;
			width: 140rpx;
			text-align: center;
		}

		.iconfont {
			font-size: 34rpx;
			color: #FA8E86;
			margin: 0 10rpx;
		}
	}

	.box {
		width: 100%;
		margin: 0 auto;
	}
	

	.width106 {
		height: auto;
		font-size: 20rpx;
		margin-right:10rpx;
		padding: 6rpx 14rpx;
	}
	
	.goods {
		width: calc(100% - 68rpx);
		height: auto;
		margin: 20rpx auto;
	}

.goods-box {
		width: 100%;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;

		.goods-li {
			flex-shrink: 0;
			width: 332rpx;
			background: rgba(255, 255, 255, 1);
			border-radius: 8rpx;
			overflow: hidden;
			margin-bottom: 34rpx;

			.img {
				width: 332rpx;
				height: 440rpx;
				background:white;
			}
		}
	}

	.width106 {
		height: auto;
		font-size: 20rpx;
		margin: 0 10rpx;
		padding: 6rpx 14rpx;


	}

	.title-box {
		width: calc(100% - 40rpx);
		padding: 20rpx 20rpx;
		padding-top: 0;

		.tips-box {
			display: flex;
		}

		.title {
			font-size: 30rpx;
			margin: 5rpx 0;
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;

		}

		.price-box {
			width: 100%;
			display: flex;
			align-items: center;

			.num {
				font-size: 32rpx;
				color: $color-primary;
			}

			.people-num {
				font-size: 20rpx;
				margin-left: 40rpx;
			}
		}
	}


	.group_box {
		width: 100%;
		display: flex;

		.group_reward {
			padding: 4rpx 10rpx;
			background: rgba(255, 221, 218, 1);
			border-radius: 19rpx;
			font-size: 22rpx;
			color: #FF7064;
		}
	}
</style>
